const ADD_MES = 'ADD-MES';

let initialState = {
    messages: [
        {id: 1, message: 'hi'},
        {id: 1, message: 'How are you'},
        {id: 1, message: 'yo'},],
    dialogs: [
        {id: 1, name: 'Svetlana'},
        {id: 2, name: 'Sasha'},
        {id: 3, name: 'Oleg'},
        {id: 4, name: 'Sasha'},
        {id: 5, name: 'Tanya'},]
}

const dialogsReducer = (state = initialState, action) => {
    switch (action.type) {
        case ADD_MES: {
            let newMes = action.newMesText;
            return { ...state,
                messages: [ ...state.messages, { id: 4, message: newMes}]
            }
        }
        default:
            return state
    }
}

export const addMesKey = (newMesText) => ({ type: ADD_MES, newMesText })

export default dialogsReducer;