import React from 'react';
import s from './Nav.module.css'
import {NavLink} from "react-router-dom";

const act = (navData) => navData.isActive ? s.active : s.item

const Nav = () => {

    return(
        <nav className={s.nav}>
          <div>
            <NavLink to="profile" className = { act }>Profile</NavLink>
          </div>
          <div>
            <NavLink to="/dialogs/*" className = { act }>Messages</NavLink>
          </div>
          <div>
            <NavLink to="news"  className = { act }>News</NavLink>
          </div>
          <div>
            <NavLink to="music" className = { act }>Music</NavLink>
          </div>
          <div>
              <NavLink to="users" className = { act }>Users</NavLink>
          </div>
            <p></p>
          <div>
            <NavLink to="settings" className = { act }>Settings</NavLink>
          </div>
        </nav>
    )
}

export default Nav;