import React from 'react';
import Header from "./Header";
import {connect} from "react-redux";
import {setUserdata, toggleIsFetching, loginOn, logout} from "../../redax/auth-reducer";
import Image from "../common/Prelouder";


class HeaderContainer extends React.Component {

    render() {
        return (
            <>
                {/*{this.props.isFetching ? <Image/> : null}*/}
                <Header {...this.props} />
            </>
        )
    }
}

const mapStateToProps = (state) => ({
    // isFetching: state.auth.isFetching,
    isAuth: state.auth.isAuth,
    login: state.auth.login,
})

export default connect(mapStateToProps, {setUserdata, toggleIsFetching, logout}) (HeaderContainer);